.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: end;
    }
    
}
.dark-mode-boton {
    filter: invert(100%);
    width: 2.5rem;
    margin-bottom: 1rem;
    cursor: pointer;
}

.barra {
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
    padding-bottom: 5rem;
    align-items: center;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        padding-bottom: 3rem;
    }
}

.logo{
    @include tablet {
        position: relative;
        top: 1rem;
    }
}

.navegacion-ul {
    padding: 0;
    list-style: none;
    height: 5rem;
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease,  opacity .3s ease;
    display: flex;
    flex-direction: column;
    text-align: center;


    @include tablet {
        display: flex;
        flex-direction: row;
        font-size: 2rem;
        opacity: 1;
        visibility: visible;
    }
}

.navegacion-ul-footer {
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    font-size: 1.5rem;

    
    li {
        padding: 0 1rem;
    }
}

.enlace-nav{
    color: $blanco;

    @include tablet {
        margin-right: 2rem;
        font-size: 1.8rem;
    }

    &:hover {
        color: $verde;
    }
}

.contact{
    margin-right: 0;
}

.menu-mobile {
    width: 2.5rem;
    margin: 1rem;
    cursor: pointer;
    @include tablet {
        display: none;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}

// Menu desplegable (Cuenta)


.cuenta-subcontent {
    display: none;
    text-align: left;

    @include tablet {
        position: absolute;
    }
}

.cuenta-subcontent a {
   display: block;
    font-size: 1.4rem;
}

.cuenta-content:hover .dropbtn {
    color: $verde;
}

.cuenta-content:hover .cuenta-subcontent {
    display: block;
}

.flecha {
    display: flex;
    align-items: center;
    color: $blanco;

    i {
        font-size: 1rem;
        padding-right: 1rem;
    }
}

.cuenta-a {
    color: $blanco;
    cursor: pointer;

    @include tablet {
        margin-right: 2rem;
    }
}

.cuenta-a a {
    margin: 0;
}

