.main-nosotros {
    margin-bottom: 10rem;
}
.contenedor-nosotros {
    @include tablet {
        @include grid(2, 2rem)
    }
    @include desktop {
        img {
            height: 100%;
        }
    }

}

.contenedor-nosotros-texto {
    padding: 2rem;
    h4 {
        font-size: 2.5rem;
        font-weight: 900;
        margin: 1rem 0;
    }
    p {
        font-size: 1.5rem;
    }
    @include desktop {
        p {
            font-size: 1.7rem;
        }
    }
}