body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.subtitulo {
  font-family: "Luckiest Guy", cursive;
  color: #FFFFFF;
  -webkit-text-stroke: 0.1px black;
  text-shadow: 0.3rem 0.3rem 0.5rem black;
  text-transform: uppercase;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-centro {
  display: flex;
  justify-content: center;
}

.sombra {
  border: 1px solid #c8c8c8;
  box-shadow: 5px 5px 13px #959595;
  margin-bottom: 2rem;
}

.boton-amarillo-block {
  display: block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-amarillo {
  display: inline-block;
  background-color: #E08709;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-verde-block {
  display: block;
  background-color: #71B100;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-verde-block:hover {
  background-color: #619800;
}

.boton-verde {
  display: inline-block;
  background-color: #71B100;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-rojo {
  display: inline-block;
  background-color: red;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #e60000;
}

.ocultar-boton {
  display: none;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  margin-bottom: 5rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    text-align: start;
    font-size: 3.3rem;
    max-width: 60rem;
  }
}

.no-webp .inicio2 {
  background-image: url(../img/header.jpg);
}

.webp .inicio2 {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  margin-bottom: 5rem;
}
.header.inicio2 {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio2 {
    background-image: none;
  }
}

footer {
  background-color: #333333;
  padding: 5rem;
  margin-top: 5rem;
}
footer p {
  text-align: center;
  color: #FFFFFF;
}
footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: end;
  }
}

.dark-mode-boton {
  filter: invert(100%);
  width: 2.5rem;
  margin-bottom: 1rem;
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 2rem;
  padding-bottom: 5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 3rem;
  }
}

@media (min-width: 768px) {
  .logo {
    position: relative;
    top: 1rem;
  }
}

.navegacion-ul {
  padding: 0;
  list-style: none;
  height: 5rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-ul {
    display: flex;
    flex-direction: row;
    font-size: 2rem;
    opacity: 1;
    visibility: visible;
  }
}

.navegacion-ul-footer {
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  font-size: 1.5rem;
}
.navegacion-ul-footer li {
  padding: 0 1rem;
}

.enlace-nav {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .enlace-nav {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
}
.enlace-nav:hover {
  color: #71B100;
}

.contact {
  margin-right: 0;
}

.menu-mobile {
  width: 2.5rem;
  margin: 1rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .menu-mobile {
    display: none;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.cuenta-subcontent {
  display: none;
  text-align: left;
}
@media (min-width: 768px) {
  .cuenta-subcontent {
    position: absolute;
  }
}

.cuenta-subcontent a {
  display: block;
  font-size: 1.4rem;
}

.cuenta-content:hover .dropbtn {
  color: #71B100;
}

.cuenta-content:hover .cuenta-subcontent {
  display: block;
}

.flecha {
  display: flex;
  align-items: center;
  color: #FFFFFF;
}
.flecha i {
  font-size: 1rem;
  padding-right: 1rem;
}

.cuenta-a {
  color: #FFFFFF;
  cursor: pointer;
}
@media (min-width: 768px) {
  .cuenta-a {
    margin-right: 2rem;
  }
}

.cuenta-a a {
  margin: 0;
}

@media (min-width: 768px) {
  .iconos-caracteristicas {
    display: flex;
  }
}

.subtitulo {
  font-weight: 700;
}

.icono {
  padding: 2rem;
}
.icono h3 {
  font-weight: 500;
}
.icono img {
  height: 10rem;
}
.icono p {
  text-align: center;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .icono p {
    font-size: 2rem;
  }
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.contenido-anuncio {
  padding: 1rem;
}
.contenido-anuncio h3 {
  font-weight: 500;
}
.contenido-anuncio .precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.iconos-anuncios {
  display: flex;
  justify-content: space-around;
  padding: 0;
  margin: 0;
}
.iconos-anuncios li {
  display: flex;
}
.iconos-anuncios li img {
  padding: 0;
  padding-right: 1rem;
}
.iconos-anuncios li p {
  font-weight: 900;
}

.contenedor-anuncio {
  max-width: 80rem;
}
.contenedor-anuncio ul {
  max-width: 28rem;
}

.main-anuncio h1 {
  font-size: 2.5rem;
}
.main-anuncio p {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .main-anuncio h1 {
    font-size: 3rem;
  }
  .main-anuncio p {
    font-size: 2rem;
  }
}

.no-webp .imagen-contactanos {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contactanos {
  background-image: url(../img/encuentra.webp);
}

.imagen-contactanos {
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
  padding: 10rem 0;
  align-items: center;
  text-align: center;
}
.imagen-contactanos h2 {
  font-weight: 700;
  color: #FFFFFF;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .imagen-contactanos h2 {
    font-size: 4rem;
  }
}
.imagen-contactanos p {
  color: #FFFFFF;
  font-size: 1.3rem;
}
@media (min-width: 768px) {
  .imagen-contactanos p {
    font-size: 1.7rem;
  }
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    grid-template-columns: 2fr 1fr;
  }
}

.entrada-blog {
  margin: 3rem 0;
}
@media (min-width: 900px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}

.texto-entrada a {
  display: block;
  padding: 1rem;
}
.texto-entrada a h3 {
  text-align: start;
  font-size: 2rem;
  font-weight: 700;
  color: #000000;
  margin: 0;
}
.texto-entrada a h3::after {
  content: "";
  display: block;
  width: 15rem;
  height: 5px;
  background-color: #71B100;
}
.texto-entrada a p {
  font-size: 1.5rem;
}

span {
  color: #71B100;
}

.testimonial {
  color: #FFFFFF;
  background-color: #71B100;
  font-weight: 500;
  font-size: 2.5rem;
  border-radius: 2rem;
  padding: 2rem;
}
.testimonial blockquote {
  position: relative;
  padding-left: 5rem;
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .testimonial blockquote {
    font-size: 2.5rem;
  }
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  display: block;
  position: absolute;
  width: 6rem;
  height: 8rem;
  left: -2rem;
}
.testimonial p {
  color: #FFFFFF;
  font-weight: 700;
}

.contenedor-blog {
  max-width: 80rem;
}

.contenedor-blog-texto {
