.formulario {
    fieldset {
        p {
            font-size: 1.5rem;
            margin: 0;
        }
    }

    legend {
        font-size: 2rem;
        padding: 1rem;
    }

    label {
        text-transform: uppercase;
        font-weight: $bold;
        display: block;
        margin-top: 1rem;
    }

    input, textarea, select {
        margin-top: 1rem;
        width: 100%;
        padding: 1rem;
        border: 1px solid $gris;
        border-radius: 1rem;
        &:hover {
            border: 1px solid darken($gris, 10);
        }
    }
    input[type=submit] {
        border: none;
    }

    textarea {
        height: 15rem;
        resize: none;
    }
    select {
        cursor: pointer;
    }

    .boton-formu {
        @include tablet {
            width: auto;
            padding: 1rem 2rem;
        }
    }
}

.form-contacto {
    display: flex;
    input {
        width: 1.3rem;
        margin-left: 1rem;
        margin-right: 3rem;
        cursor: pointer;
    }
}

#fecha, #hora {
    cursor: pointer;
}


