.seccion-inferior {
    @include tablet {
        @include grid(2, 2rem);
        grid-template-columns: 2fr 1fr;
    }
}
.entrada-blog {
    margin: 3rem 0;
    @include tabletXL {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
}

.texto-entrada {
    a {
        display: block;
        padding: 1rem;
        h3 {
            text-align: start;
            font-size: 2rem;
            font-weight: $bold;
            color: $negro;
            margin: 0;

            &::after {
                content: '';
                display: block;
                width: 15rem;
                height: 5px;
                background-color: $verde;
            }
        }
        p {
            font-size: 1.5rem;
        }
    }
}
span {
    color: $verde;
}

//TESTIMONIOS


.testimonial {
    color: $blanco;
    background-color: $verde;
    font-weight: 500;
    font-size: 2.5rem;
    border-radius: 2rem;
    padding: 2rem;

    blockquote {
        position: relative;
        padding-left: 5rem;
        font-size: 2rem;

        @include desktop {
            font-size: 2.5rem;
        }
        
        &::before {
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            display: block;
            position: absolute;
            width: 6rem;
            height: 8rem;
            left: -2rem;
        }
    }
    p {
        color: $blanco;
        font-weight: $bold;
    }
}

.contenedor-blog {
    max-width: 80rem;
}
.contenedor-blog-texto {
    padding: 0 2rem;
}
.main-entrada {
    h1 {
        font-size: 2.5rem;
    }
    p {
        font-size: 1.5rem;
    }
    @include tablet {
        h1 {
            font-size: 3.5rem;
        }
        p {
            font-size: 2rem;
        }
    }
}






























