h1, h2 {
    font-size: 2.5rem;
    @include tablet {
        font-size: 4rem;
    }
}

.alerta {
    
    &.error {
        background-color: $rojo;
        text-transform: uppercase;
        font-weight: $bold;
        text-align: center;
        color: $blanco;
        padding: .7rem;
        margin: 1.5rem 0;
        box-shadow: 2px 2px 5px black;
        border-radius: 1rem;
        font-size: 1.3rem;
    }

    &.correcto {
        background-color: $verde;
        text-transform: uppercase;
        font-weight: $bold;
        text-align: center;
        color: $blanco;
        padding: .7rem;
        margin: 1.5rem 0;
        box-shadow: 2px 2px 5px black;
        border-radius: 1rem;
        font-size: 1.3rem;
    }
    &.actualizado {
        background-color: orange;
        text-transform: uppercase;
        font-weight: $bold;
        text-align: center;
        color: $blanco;
        padding: .7rem;
        margin: 1.5rem 0;
        box-shadow: 2px 2px 5px black;
        border-radius: 1rem;
        font-size: 1.3rem;
    }

    @include tablet {
        &.correcto, &.error, &.actualizado {
            font-size: 1.6rem;
        }
    }
}

.botones-crear a{
    padding: 0.5rem;
    font-size: 1.3rem;
    @include tablet() {
        padding: 1rem 3rem;
        font-size: 1.5rem;
    }
}

.boton-rojo {
    padding: 0.5rem 1rem;
    font-size: 1.3rem;
    @include tablet() {
        padding: .7rem 2rem;
        font-size: 1.5rem;
    }
}

table.propiedades {
    width: 100%;
    margin-top: 4rem;
    border: 1px solid black;
    border-spacing: 0;
    @include tablet {
        font-size: 2rem;
    }

    thead {
        background-color: darken($gris, 60);

        th {
            border: 1px solid black;
            color: $blanco;
            padding: 1rem;
            font-size: 1.3rem;

            @include tablet {
                padding: 2rem;
                font-size: 2rem;
            }
        }
    }

    tbody {
        background-color: darken($blanco, 10);
        box-shadow: .5rem .5rem 3rem rgb(0, 0, 0);
        
        td {
            border: 1px solid black;
            padding: 0 .5rem;
            font-size: 1rem;
            text-align: center;
            font-weight: $bold;
            a {
                width: 80%;
                margin: .5rem 0;
                padding: 0.5rem 0;
                font-size: 1rem;
                @include tablet {
                    font-size: 2rem;
                }
            }
            @include tablet {
                font-size: 2rem;
            }
        }

        .imagen-tabla {
            width: 8rem;
            margin: auto;
            @include tablet {
                width: 20rem;
                padding: 1rem 0;
            }
        }
    }
}

.imagen-actu {
    width: 20rem;
}

// Boton Eliminar
.form-eliminar {
    input {
        width: 80%;
        height: 2.8rem;
        padding: 1rem 0;
        font-size: 1rem;
        @include tablet {
            font-size: 2rem;
            height: 100%;
        }
    }
    .boton-rojo {
        margin-top: 0;
        margin-bottom: .5rem;
    }
}