.no-webp .inicio {
    background-image: url(../img/header.jpg);
}

.webp .inicio {
    background-image: url(../img/header.webp);
}

.header {
    background-color: $grisOscuro;
    margin-bottom: 5rem;
    &.inicio {
        background-position: center center;
        background-size: cover;

        @include tablet {
            min-height: 70rem;
        }

        .contenido-header {
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h1 {
        color: $blanco;
        font-weight: $bold;
        font-size: 2rem;

        @include tablet {
            text-align: start;
            font-size: 3.3rem;
            max-width: 60rem;
        }
    }
}

// INICIO 2
.no-webp .inicio2 {
    background-image: url(../img/header.jpg);
}

.webp .inicio2 {
    background-image: url(../img/header.webp);
}

.header {
    background-color: $grisOscuro;
    margin-bottom: 5rem;
    &.inicio2 {
        background-position: center center;
        background-size: cover;

        @include tablet {
            background-image: none;
        }
    }
}

