.no-webp .imagen-contactanos {
    background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contactanos {
    background-image: url(../img/encuentra.webp);
}

.imagen-contactanos {
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    display: flex;
    flex-direction: column;
    padding: 10rem 0 ;
    align-items: center;
    text-align: center;

    h2{
        font-weight: $bold;
        color: $blanco;
        font-size: 3rem;
        @include tablet {
            font-size: 4rem;
        }
    }

    p {
        color: $blanco;
        font-size: 1.3rem;
        @include tablet {
            font-size: 1.7rem;
        }
    }
}


