.dark-mode {
    #text {
        color: $blanco;
    }
    pre {
        color: $blanco;
    }
    background-color: darken($grisOscuro, 10);

    h1, h2, h3, h4 {
        color: darken($gris, 5);
    }
    p {
        color: $blanco;
    }
    .texto-entrada {
        h3 {
            color: darken($gris, 5);
        }
    }
    
    .iconos-anuncios {
        .icono {
            filter: invert(100%);
        }
    }

    .formulario {
        legend, label, input, p, select, textarea {
            color: $blanco;
        }

        input:not([type=submit]) , textarea, select {
            background-color: $grisOscuro;
            border: none;
        }
    }

    .sombra {
        border: 1px solid #000000;
        box-shadow: 5px 5px 13px #000000;
    }

    .propiedades {
        tbody {
            color: $blanco;
            background-color: rgb(49, 48, 48);
        }
    }
}
