.contenedor-anuncios {
    @include tablet {
        @include grid(3, 2rem)
    }
}

.contenido-anuncio {
    padding: 1rem;

    h3 {
        font-weight: 500;
    }
    .precio{
        color: $verde;
        font-weight: $bold;
        font-size: 3rem;
        margin: 0;
    }

}

.iconos-anuncios {
    display: flex;
    justify-content: space-around;
    padding: 0;
    margin: 0;
    li {
        display: flex;
        img {
            padding: 0;
            padding-right: 1rem;
        }
        p {
            font-weight: 900;
        }
    }
}

.contenedor-anuncio {
    max-width: 80rem;
    ul {
        max-width: 28rem;
    }
}

.main-anuncio {
    h1 {
        font-size: 2.5rem;
    }
    p {
        font-size: 1.5rem;
    }

    @include tablet {
        h1 {
            font-size: 3rem;
        }
        p {
            font-size: 2rem;
        }
    }
}