.iconos-caracteristicas {
    
    @include tablet {
        display: flex;
    }
}

.subtitulo {
    font-weight: $bold;
}

.icono {
    padding: 2rem;
    h3 {
        font-weight: 500;
    }
    img {
        height: 10rem;
    }
    p {
        text-align: center;
        font-size: 1.5rem;
        @include tablet {
            font-size: 2rem;
        }
    }
}